body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.project-card {
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}

.project-card:hover {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
}
.skill-badge {
    padding: 5px 10px;
    margin: 5px;
    border-radius: 5px;
    display: inline-block;
    text-align: center;
}

.skill-badge-blue {
    background-color: #007bff;
    color: white;
}

.skill-badge-green {
    background-color: #28a745;
    color: white;
}

.skill-badge-orange {
    background-color: #fd7e14;
    color: white;
}

.skill-badge-red {
    background-color: #dc3545;
    color: white;
}
.carousel {
    max-width: 1200px;
    margin: 0 auto;
}

.carousel-image {
    height: auto;
    max-height: 600px;
    object-fit: cover;
}

.carousel-caption {
    background: rgba(0, 0, 0, 0.6);
    padding: 20px;
    border-radius: 10px;
}

.carousel-caption h3 {
    font-size: 1.75rem;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 10px;
}

.carousel-caption p {
    font-size: 1.1rem;
    line-height: 1.5;
}
.carousel-indicators {
    position: relative;
    bottom: -5px;
}

.carousel-indicators li {
    background-color: #ccc;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    margin: 0 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.carousel-indicators .active {
    background-color: #000000;
}
.home-container {
    padding: 1rem;
}

.about-me {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
}

.about-me-image-container {
    flex: 1 1;
    text-align: center;
}

.about-me-image {
    max-width: 100%;
    height: auto;
    border-radius: 50%;
}

.about-me-content {
    flex: 2 1;
    font-size: 1rem;
}
.project-card {
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}

.project-card:hover {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
}
.carousel {
    max-width: 800px;
    margin: 0 auto;
}

.carousel-image {
    height: auto;
    max-height: 600px;
    object-fit: cover;
}

.styled-div {
    font-family: Arial, sans-serif;
    font-size: 18px;
    line-height: 1.6;
    color: #333;
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    max-width: 600px;
    margin: 0 auto;
}

.styled-div strong {
    color: #007BFF;
}

.styled-div a {
    color: #007BFF;
    text-decoration: none;
}

.styled-div a:hover {
    text-decoration: underline;
}
